@font-face {
  font-family: Speedy Font;
  src: url("SpeedyRegular-7BLoE.1e2a4539.woff2");
}

@font-face {
  font-family: Nerd Font;
  src: url("nerdFont.2fc33cf9.woff2");
}

#calculator {
  outline-offset: -1px;
  aspect-ratio: 3 / 4;
  user-select: none;
  background-color: #a9a9a9;
  outline: 1px solid #a9a9a9;
  flex-flow: column;
  max-width: fit-content;
  height: 80vh;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &.edit-mode {
    outline-color: red;
  }

  & *, & .speedy-font {
    font-family: Speedy Font, sans-serif;

    &.nerd-font {
      font-family: Nerd Font, sans-serif;
    }
  }

  & > #visor {
    z-index: -1;
    background-color: #fff;
    flex: .3;
    justify-content: space-between;
    align-items: center;
    gap: .5rem;
    padding: 0 2rem;
    font-size: 3rem;
    display: flex;
    position: relative;

    & > #alternate-numbers {
      border-right: 1px solid #000;
      flex-flow: column;
      flex: 1;
      display: flex;

      &.hide-alternate-numbers {
        opacity: 0;
      }

      & > .alternate-number {
        white-space: nowrap;
        scrollbar-width: none;
        text-overflow: ellipsis;
        cursor: pointer;
        max-width: 5ch;
        padding: .5rem 1rem;
        font-size: .3em;
        font-weight: 100;
        overflow-x: scroll;

        &.active {
          font-size: .45em;
          font-weight: 900;
        }
      }
    }

    & > #operator-display {
      font-size: .7em;
      position: absolute;
      top: .5rem;
      right: .5rem;
    }

    & > #number-display {
      scrollbar-width: none;
      flex-direction: row-reverse;
      flex: 5;
      justify-content: flex-start;
      display: flex;
      position: relative;
      overflow-x: scroll;

      & > #extras {
        z-index: 1;
        flex-flow: column;
        font-size: .5em;
        display: flex;
        position: fixed;
        top: .5rem;
        left: .5rem;

        & > #copy-clipboard {
          color: #c427aa;
          z-index: 2;
          -webkit-text-stroke-width: 1px;
          opacity: .3;
          transition: opacity .1s ease-in, transform .1s;
          transform: scale(.8);

          &:hover {
            cursor: pointer;
            opacity: 1;
            -webkit-text-stroke-color: gold;
            transform: scale(1);
          }
        }

        & > #hide-alternate-numbers {
          color: #c427aa;
          z-index: 2;
          -webkit-text-stroke-width: 1px;
          opacity: .3;
          transition: opacity .1s ease-in, transform .1s;
          transform: scale(.8);

          &:hover {
            cursor: pointer;
            opacity: 1;
            -webkit-text-stroke-color: gold;
            transform: scale(1);
          }
        }
      }

      & > p.numberOutput {
        user-select: text;
        width: max-content;
        margin: 0;
      }
    }
  }

  & > #buttons {
    flex: 1;
    grid-template: repeat(5, 1fr) / repeat(4, 1fr);
    margin-top: .5rem;
    display: grid;

    & button:is(.number, .operator, .special) {
      border: 1px solid #a9a9a9;
      font-size: 1.7rem;
      transition: background-color 80ms ease-out;
    }

    & button.number {
      background-color: #fff;

      &:hover {
        background-color: #d0e8ff;
      }

      &:active {
        background-color: #b0c8ff;
      }
    }

    & button.operator {
      color: #fff;
      background-color: #000;

      &:hover {
        background-color: #708090;
      }

      &:active {
        background-color: #506090;
      }
    }

    & button.special {
      color: #fff;
      background-color: #d43775;

      &:hover:not([disabled]) {
        background-color: #c427aa;
      }

      &:active:not([disabled]) {
        background-color: #a4078a;
      }
    }

    & #positions {
      grid-template: repeat(2, 1fr) / repeat(2, 1fr);
      display: grid;
    }

    & > #main-pad {
      grid-template: repeat(4, 1fr) / repeat(3, 1fr);
      grid-area: 1 / 1 / span 4 / span 3;
      display: grid;

      & > button.special {
        order: 1;
      }
    }

    & > #side-pad {
      flex-flow: column;
      grid-area: 1 / -2 / -1 / -1;
      display: flex;

      & > * {
        flex: 1;
      }
    }

    & > #bottom-pad {
      grid-area: -2 / 1 / -1 / span 3;
      grid-template-columns: repeat(3, 1fr);
      display: grid;

      & > * {
        flex: 1;
      }
    }
  }
}
